/* eslint-disable no-restricted-globals */

import React, { Component } from 'react'
import { Link} from "react-router-dom";
export default class Nav extends Component {
    redirect() {
       window.location.replace(window.location.origin+ "/mua-ve");
    }

    render() {
        return (
            <header className="section header mb-2">
                <div className="container">
                    <nav className="navbar navbar-expand-lg navbar-light bg-light">
                        <Link to="" className="navbar-brand">SANJALIKA</Link>
                        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                            <span className="navbar-toggler-icon" />
                        </button>
                        <div className="collapse navbar-collapse" id="navbarNav">
                            <ul className="navbar-nav menu-right">
                                <li className="nav-item active">
                                    <Link to="/" className="nav-link">HOME</Link>
                                </li>
                                <li className="nav-item">
                                    <Link to="/News" className="nav-link">NEWS</Link>
                                </li>
                                <li className="nav-item">
                                    <div className="menu-product">
                                        <Link to="#" className="nav-link layer1">EXPERIENCE</Link>
                                        <div className="layer2">
                                            <ul>
                                                <li><Link to="/DragonPark">DRAGON PARK</Link></li>
                                                <li><Link to="/WaterPark">WATER PARK</Link></li>
                                                <li><Link to="/QueenCableCars">QUEEN CABLE CARS &amp; THE MYSTERIOUS HILLS</Link></li>
                                                <li><Link to="/LightHouse">SANJALIKA LIGHTHOUSE</Link></li>
                                                <li><Link to="/Beach">SANJALIKA BEACH</Link></li>
                                                <li><Link to="/Gallery">SANJALIKA GALLERRY</Link></li>
                                                <li><Link to="/OldQuarterKiosks">SANJALIKA OLD QUARTER KIOSKS</Link></li>
                                                <li><Link to="/FoodZone">FOOD ZONE</Link></li>
                                            </ul>
                                        </div>
                                    </div>
                                </li>
                                <li className="nav-item">
                                    <Link to="/AboutUs" className="nav-link">ABOUT US</Link>
                                </li>
                                <li className="nav-item">
                                    <Link to="/ContactUs" className="nav-link">CONTACT US</Link>
                                </li>
                                <li className="nav-item buy-ticket">
                                    <Link to="" onClick={this.redirect} className="nav-link">BUY TICKET</Link>
                                </li>
                            </ul>
                        </div>
                    </nav>
                </div>
            </header>
        )
    }
}
